import { getPublicKey, nip19 } from "nostr-tools";
import { Button } from "primereact/button";
import { Toast } from "primereact/toast";
import { useRef } from "react";
import { FieldValues, useForm } from "react-hook-form";
import { Link } from "react-router-dom";
import { GenericObject } from "../../../../data/types/utils";
import { FormField } from "../../../common/atoms/FormField";
import "./style.scss";

export function SignIn() {
  const toastRef = useRef<Toast>(null);

  const defaultValues: GenericObject = {
    nostrKey: "",
  };

  const {
    control,
    formState: { errors },
    handleSubmit,
    getValues,
    reset,
  } = useForm({ defaultValues });

  const onSignIn = (data: FieldValues) => {
    console.log({ data });
    try {
      const privateKey = nip19.decode(data.nostrKey).data;
      const publicKey = getPublicKey(privateKey as string);
      console.log({ privateKey, publicKey });
    } catch (error: unknown | { [key: string]: any }) {
      toastRef.current?.show({
        severity: "error",
        summary: "Provided key is invalid!",
        detail: "You must provide a valid nsec",
      });
      console.log({ error });
    }
  };

  return (
    <div className="page sign-in">
      <h1>SignIn</h1>
      <div className="signin-form">
        <Toast ref={toastRef} />
        <form onSubmit={handleSubmit(onSignIn)}>
          <FormField
            name="nostrKey"
            label="Nostr key (nsec)"
            control={control}
            errors={errors}
          />
          <Button label="Sign in" type="submit" />
        </form>
      </div>
      <span>
        Don't have Nostr keys yet? Sign up <Link to="/sign-up">here</Link>
      </span>
    </div>
  );
}
